.form {
    margin: 5px auto;
    background-color: white;
    width: 700px;
    box-shadow: 1px 5px 15px -2px
}

@media (max-width: 680px) {
    .form {
        width: 400px;
    }
}

@media (max-width: 450px) {
    .form {
        width: 300px;
    }
}

.form__legend {

    text-align: center;
    color: black;
    padding: 10px;
    padding-bottom: 2px;
    font-size: 20px;
    font-weight: 800;
}


.form__fieldset {
    border: none;
    border-top: 1px solid rgb(23, 221, 162);
    font-size: 15px;
    padding: 5px 0px 5px 0px;
    margin: 5px 0 1px;

}

.form__field {
    border: 1px solid rgba(196, 195, 195, 0.678);
    padding: 5px;
    margin: 10px;
    max-width: 550px;
    width: 100%;
    height: 30px;

}

@media (max-width: 680px) {
    .form__field {
        max-width: 250px;
    }
}

@media (max-width: 450px) {
    .form__field {
        max-width: 280px;
        text-align: center;
       
    }
}

.form__button {
    border: 1px solid rgb(23, 221, 162);
    padding: 5px;
    color: white;
    background-color: rgb(23, 221, 162);
    font-size: 15px;
    font-weight: 800;
    height: 30px;
    cursor: pointer;
}

.form__button:hover {
    transform: scale(1.1);

}

.form__button:active {

    transform: scale(1.3);
}

@media (max-width: 450px) {
    .form__button {
        width: 300px;
        text-align: center;

    }

    .form__button:hover {
        transform: scale(0.9);

    }

    .form__button:active {

        transform: scale(0.88);
    }
}

