.list {
    list-style-type: none;
    margin: 0px auto 10px;
    padding: 30px;
    background-color: white;
    width: 700px;
    box-shadow: 1px 5px 15px -2px
}
@media (max-width: 680px) {
    .list {
       max-width: 400px;
    }
  }
  @media (max-width: 450px) {
    .list {
      width: 300px;
    }
  }


.list__item {
    border-bottom: 2px solid rgb(23, 221, 162);
    padding: 7px;
    margin: 10px;
    display: grid;
    grid-template-columns: 30px minmax(auto, 485px) 30px;
    grid-gap: 20px 20px;
    align-items: center;


}


.list__item--done {
    text-decoration: line-through;
    color: rgba(23, 221, 162, 0.432);

}

.list__content {
    padding: 3px 30px;
    word-break: break-word;
    text-align: justify;
}


.button__remove {
    padding: 3px;
    background-color: rgba(248, 16, 16, 0.726);
    font-size: 15px;
    font-weight: 900;
    color: white;
    border: 0;
    height: 30px;
    width: 50px;
    transition: transform 0.5s;
    cursor: pointer;
}

.button__remove:hover {
   /* transform: scale(1.1); */
    transform: rotate(10deg);
    background-color: rgb(248, 16, 16);
}

.button__remove:active {
     transform: scale(0.95);
     transform: rotate(20deg); 
  
}


.button__done {
    padding: 3px;
    background-color: rgb(27 209 88 / 80%);
    padding: 5px;
    font-size: 15px;
    font-weight: 900;
    color: rgb(10, 10, 10);
    border: 0;
    height: 30px;
    width: 50px;
    cursor: pointer;

}

.button__done:hover {
    transform: scale(1.1);
    background-color: rgb(27, 209, 88);
}

.button__done:active {
    transform: scale(1.3);
}

.hiddenButton--hidden {
    display: none;
}